      <!-- Êõ¥ÊîπËÉåÊôØ -->
      <div class="bg clearfix">
        <div class="fr cbackground">
          <div class="cb" >Êõ¥Êç¢ËÉåÊôØ</div>
          <van-uploader class="upPic" :max-size="maxSize" :after-read="upLoadBg"/>
        </div>
      </div>
      <div class="user">
        <div class="clearfix">
          <div class="myPic">
              <img class="auto-img" :src="userInfo.userImg" alt="">
          </div>
          <div class="myName">{{userInfo.nickName}}</div>
        </div>
        <div class="desc">{{userInfo.desc == '' ? 'Âø´Êù•Áïô‰∏ã‰Ω†ÁöÑËÆ∞ÂΩïÂêßüíó' : userInfo.desc}}</div>
      </div>
    </div>
    <van-cell-group>
      <van-cell :title="item.title"  v-for="(item, index) in mylist" :key="index" @click="goManage(item.routerName)"/>
    </van-cell-group>

    <!-- <div class="hots">
        <div class="recommond" >--‰∏∫‰Ω†Êé®Ëçê--</div>
        <div
        class="fl content-item"
        :class="[index % 2 == 0 ? 'left' : 'right']"
        v-for="(item, index) in hotProducts"
        :key="index"
      >
        <div @click="goDetail(item.pid)">
          <img class="auto-img" v-lazy="item.smallImg" alt />
        </div>
        <div class="pro-name one-text">{{item.name}}</div>
        <div class="pro-enname one-text">{{item.enname}}</div>
        <div class="pro-price-box">
          <div class="fl pro-price">Ôø•{{item.price}}</div>

        </div>
      </div>
    </div> -->
    
  </div>
</template>

<script>
import "../../assets/css/my.less";
export default {
  data() {
    return {
      mylist: [
        { title: "üíóÊàëÁöÑÊî∂Ëóè" ,routerName:'myCollect'},
        { title: "üìëÊàëÁöÑËÆ¢Âçï" ,routerName:'myLists'},
        { title: "üíºË¥¶Âè∑ÁÆ°ÁêÜ" ,routerName:'myAccount'},
        { title: "üåéÂú∞ÂùÄÁÆ°ÁêÜ" ,routerName:'myAaddress'},   
        { title: "üö®ÂÆâÂÖ®‰∏≠ÂøÉ" ,routerName:'mySecure'}
      ],
      userInfo: {},
      //ÈôêÂà∂Êñá‰ª∂Â§ßÂ∞è
      maxSize: 1 * 1024 * 1024,
      hotProducts:[]
    };
  },
  created() {
    //ÂàùÂßãÂåñËé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    this.getUser();
    //this.getHot({ type: "isHot" });
  }, 
  
  methods: {
   
    //Ëé∑ÂèñÁî®Êà∑
    getUser() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'A001') {
           
            this.userInfo = result.data.result[0];
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //‰∏ä‰º†ËÉåÊôØ .Êñá‰ª∂‰∏ä‰º†ÂêéÊâßË°å
    upLoadBg(file){
      let baseImg = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');
      //Â§ÑÁêÜÂõæÁâáÁ±ªÂûãÊ†ºÂºè
      let imgType = file.file.type.split('/')[1];
      
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img:baseImg,
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'I001') {
    
            this.userInfo.userBg = result.data.userBg;
          }else{
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //ÁÆ°ÁêÜ
    goManage(name){
      this.$router.push({name});
    },
    //Ëé∑Âèñ ÊúÄÊñ∞ÂïÜÂìÅ
    
  }
};
</script>

